import { Grid, Card, CardHeader, CardMedia, CardContent, CardActions, Collapse, IconButton, Typography } from "@mui/material";
import * as React from 'react';
import { styled } from '@mui/material/styles';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import FlightTakeoffIcon from '@mui/icons-material/FlightTakeoff';
import FlightLandIcon from '@mui/icons-material/FlightLand';
import { uni_font_family, uni_font_color, uni_font_color_sub_1 } from "../constants";

const ExpandMore = styled((props) => {
    const { expand, ...other } = props;
    return <IconButton {...other} />;
})(({ theme, expand }) => ({
    transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
    }),
}));

const Homepage = () => {
    const [expanded, setExpanded] = React.useState(false);

    const handleExpandClick = () => {
        setExpanded(!expanded);
    };

    return (
        <Grid container>
            <Grid item xs={6}>
                <Card sx={{ maxWidth: 345 }}>
                    <CardHeader
                        title={<Typography fontSize={24} color={uni_font_color} fontFamily={uni_font_family} fontWeight="bold">
                            <FlightTakeoffIcon />HK......<FlightLandIcon />CA
                        </Typography>}
                        subheader={<Typography fontSize={12} color={uni_font_color_sub_1} fontFamily={uni_font_family}>
                            July, 2023
                        </Typography>}
                    />
                    <CardMedia
                        component="img"
                        height="194"
                        image="../../media/1.jpg"
                        alt="1.jpg"
                    />
                    <CardContent>
                        <Typography variant="body2" color="text.secondary" fontFamily={uni_font_family}>
                            Left previous position voluntarily to pursue personal and professional growth in Canada
                        </Typography>
                    </CardContent>
                    <CardActions disableSpacing>
                        <ExpandMore
                            expand={expanded}
                            onClick={handleExpandClick}
                            aria-expanded={expanded}
                            aria-label="show more"
                        >
                            <ExpandMoreIcon />
                        </ExpandMore>
                    </CardActions>
                    <Collapse in={expanded} timeout="auto" unmountOnExit>
                        <CardContent>
                            <Typography paragraph variant="body2" fontFamily={uni_font_family}>I made a deliberate decision to depart from my previous position as a full-stack application developer in Hong Kong, opting instead to embark on a journey of personal and professional growth in Canada.</Typography>
                            <Typography paragraph variant="body2" fontFamily={uni_font_family}>My motivation behind this significant change was rooted in a deep appreciation for the slower-paced rhythm of life and the enriching living environment that Canada offers. This transition reflects my commitment to embracing new challenges and opportunities while seeking an environment that aligns with my values and aspirations.</Typography>
                            <Typography paragraph variant="body2" fontFamily={uni_font_family}>As a dedicated and adaptable professional, I am eager to apply the skills and experiences I've gained from my previous role to contribute to innovative projects and bring a fresh perspective to the Canadian tech industry.</Typography>
                            <Typography paragraph variant="body2" fontFamily={uni_font_family}>I am excited to connect with employers who value initiative, diversity, and a passion for continuous improvement. I believe my journey has equipped me with a unique blend of experiences that can enrich their teams and projects.</Typography>
                        </CardContent>
                    </Collapse>
                </Card>
            </Grid>
            <Grid item xs={6}>
                4-yrs experienced software development, 1-yr experienced Full Stack App Dev
            </Grid>
            <Grid item xs={6}>
                Willing to learn anything new, prefer to work as full stack developer / software developer / DevOps engineer
            </Grid>
            <Grid item xs={6}>
                Love to connect everything with applications to smoothen steps and processes
            </Grid>
            <Grid item xs={6}>
                Pro√¨cient in C# (.NET, REST APIs), ReactJS (Redux, Material-UI)
            </Grid>
            <Grid item xs={6}>
                Skilled with MySQL, DevOps (Docker, Rancher, GitLab CI/CD, YAML), Python, Java, C++
            </Grid>
            <Grid item xs={6}>
                Effective team collaborator w/ help desk support exp.
            </Grid>
        </Grid>
    );
}

export default Homepage;